<template>
  <div v-if="results" class="reusable-results">
    <!-- Technical Error Alert -->
    <div v-if="hasTechnicalError" class="alert alert-warning">
      <i class="fas fa-exclamation-triangle me-2"></i>
      Some citations could not be verified due to technical issues. Please try again later or contact support if the problem persists.
    </div>
    
    <div class="card mt-4">
      <div class="card-header bg-primary text-white">
        <h5 class="mb-0">Citation Analysis Results</h5>
      </div>
      
      <div class="card-body">
        <!-- Success Message -->
        <div class="alert alert-success mb-3">
          <h5><i class="fas fa-check-circle me-2"></i>Analysis Complete</h5>
          <p class="mb-0">Found {{ totalCitations }} citations in the document.</p>
        </div>
        
        <!-- Summary Stats -->
        <div class="citation-stats mb-4">
          <div class="row g-3">
            <div class="col-md-4">
              <div class="card bg-light">
                <div class="card-body text-center">
                  <h6 class="card-subtitle mb-1 text-muted">Total Citations</h6>
                  <h3 class="mb-0">{{ totalCitations }}</h3>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="card bg-light">
                <div class="card-body text-center">
                  <h6 class="card-subtitle mb-1 text-muted">Verified</h6>
                  <h3 class="mb-0 text-success">{{ verifiedCount }}</h3>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="card bg-light">
                <div class="card-body text-center">
                  <h6 class="card-subtitle mb-1 text-muted">Unverified</h6>
                  <h3 class="mb-0 text-danger">{{ unverifiedCount }}</h3>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Tabs for Different Citation Statuses -->
        <ul class="nav nav-tabs mt-4" role="tablist">
          <li class="nav-item" role="presentation">
            <button class="nav-link" :class="{ active: activeTab === 'all' }" @click="activeTab = 'all'" type="button">
              All Citations <span class="badge bg-secondary ms-1">{{ totalCitations }}</span>
            </button>
          </li>
          <li class="nav-item" role="presentation">
            <button class="nav-link" :class="{ active: activeTab === 'courtlistener' }" @click="activeTab = 'courtlistener'" type="button">
              <i class="fas fa-check-circle text-success me-1"></i> CourtListener
              <span class="badge bg-success ms-1">{{ courtListenerCount }}</span>
            </button>
          </li>
          <li class="nav-item" role="presentation">
            <button class="nav-link" :class="{ active: activeTab === 'verified' }" @click="activeTab = 'verified'" type="button">
              <i class="fas fa-check text-primary me-1"></i> Other Verified
              <span class="badge bg-primary ms-1">{{ otherVerifiedCount }}</span>
            </button>
          </li>
          <li class="nav-item" role="presentation">
            <button class="nav-link" :class="{ active: activeTab === 'unverified' }" @click="activeTab = 'unverified'" type="button">
              <i class="fas fa-exclamation-triangle text-warning me-1"></i> Unverified
              <span class="badge bg-warning text-dark ms-1">{{ unverifiedCount }}</span>
            </button>
          </li>
          <li class="nav-item" role="presentation" v-if="hasUnlikelyCitations">
            <button class="nav-link" :class="{ active: activeTab === 'unlikely' }" @click="activeTab = 'unlikely'" type="button">
              <i class="fas fa-question-circle text-danger me-1"></i> Questionable
              <span class="badge bg-danger ms-1">{{ results.unlikely_citations.length }}</span>
            </button>
          </li>
        </ul>
        <div class="tab-content mt-3">
          <!-- All Citations Tab -->
          <div class="tab-pane fade" :class="{ show: activeTab === 'all', active: activeTab === 'all' }">
            <CitationTable :citations="filteredCitations('all')" />
          </div>
          
          <!-- CourtListener Verified Tab -->
          <div class="tab-pane fade" :class="{ show: activeTab === 'courtlistener', active: activeTab === 'courtlistener' }">
            <CitationTable :citations="filteredCitations('courtlistener')" 
                         empty-message="No citations verified by CourtListener found." />
          </div>
          
          <!-- Other Verified Tab -->
          <div class="tab-pane fade" :class="{ show: activeTab === 'verified', active: activeTab === 'verified' }">
            <CitationTable :citations="filteredCitations('verified')" 
                         empty-message="No other verified citations found." />
          </div>
          
          <!-- Unverified Tab -->
          <div class="tab-pane fade" :class="{ show: activeTab === 'unverified', active: activeTab === 'unverified' }">
            <CitationTable :citations="filteredCitations('unverified')" 
                         empty-message="No unverified citations found." />
          </div>
          <!-- Unlikely Citations Tab -->
          <div class="tab-pane fade" :class="{ show: activeTab === 'unlikely', active: activeTab === 'unlikely' }">
            <div v-if="results.unlikely_citations && results.unlikely_citations.length">
              <h6>Unlikely Citations (Artifacts or Invalid):</h6>
              <div class="table-responsive">
                <table class="table table-striped table-hover">
                  <thead>
                    <tr>
                      <th>Citation Text</th>
                      <th>Reason</th>
                      <th>Metadata</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(citation, idx) in results.unlikely_citations" :key="'unlikely-'+idx">
                      <td>{{ citation.citation_text }}</td>
                      <td>
                        <span class="badge bg-warning text-dark">
                          {{ citation.metadata && citation.metadata.invalid_reason ? citation.metadata.invalid_reason : 'Unknown' }}
                        </span>
                      </td>
                      <td>
                        <pre class="mb-0">{{ citation.metadata }}</pre>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div v-else class="alert alert-info">No unlikely citations detected.</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CitationTable from './CitationTable.vue';

export default {
  name: 'ReusableResults',
  components: {
    CitationTable
  },
  props: {
    results: {
      type: Object,
      required: true
    },
    hasTechnicalError: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeTab: 'all' // Default to showing all citations
    };
  },
  computed: {
    totalCitations() {
      return this.results.validation_results?.length || 0;
    },
    verifiedCount() {
      if (!this.results.validation_results) return 0;
      return this.results.validation_results.filter(c => {
        const formatted = this.formatCitation(c);
        return formatted?.verified === true;
      }).length;
    },
    unverifiedCount() {
      if (!this.results.validation_results) return 0;
      return this.results.validation_results.filter(c => {
        const formatted = this.formatCitation(c);
        return formatted?.verified !== true;
      }).length;
    },
    courtListenerCount() {
      return this.filteredCitations('courtlistener').length;
    },
    otherVerifiedCount() {
      return this.filteredCitations('verified').length - this.courtListenerCount;
    },
    hasUnlikelyCitations() {
      return this.results.unlikely_citations && this.results.unlikely_citations.length > 0;
    }
  },
  methods: {
    formatCitation(citation) {
      try {
        // Handle Vue Proxy objects by getting the raw value
        const rawCitation = citation?.__v_raw ? citation.__v_raw : citation;
        
        // If citation is a string, try to parse it as JSON
        if (typeof rawCitation === 'string') {
          try {
            citation = JSON.parse(rawCitation);
          } catch (e) {
            console.warn('Failed to parse citation as JSON:', rawCitation);
            return null;
          }
        } else {
          citation = rawCitation;
        }

        // Handle case where citation is already in the correct format
        if (citation?.citation_text || citation?.text || citation?.citation?.citation_text) {
          const citationText = citation.citation_text || citation.text || citation.citation?.citation_text;
          const caseName = citation.case_name || citation.citation?.case_name || 
                         citation.metadata?.resolved_case_name || 'N/A';
          const validation = citation.validation || citation.citation?.validation || {};
          
          return {
            ...citation,
            ...citation.citation, // Spread citation details if they're nested
            citation: citationText,
            case_name: caseName,
            verified: validation.verified || citation.verified || false,
            validation_method: validation.validation_method || 
                            citation.validation_method || 
                            (validation.verified ? 'Other' : null)
          };
        }

        // If we get here, the citation format is unexpected
        console.warn('Unexpected citation format:', citation);
        return null;
      } catch (error) {
        console.error('Error formatting citation:', error, citation);
        return null;
      }
    },
    
    filteredCitations(filter) {
      if (!this.results.validation_results) return [];
      
      // Format all citations first
      const formattedCitations = this.results.validation_results
        .map(citation => this.formatCitation(citation))
        .filter(citation => citation !== null);
      
      switch (filter) {
        case 'all':
          return formattedCitations;
        case 'courtlistener':
          return formattedCitations.filter(c => 
            c.verified && c.validation_method === 'CourtListener'
          );
        case 'verified':
          return formattedCitations.filter(c => c.verified);
        case 'unverified':
          return formattedCitations.filter(c => !c.verified);
        default:
          return formattedCitations;
      }
    },
    getBadgeClass(method) {
      // Map validation methods to badge classes
      const methodMap = {
        'CourtListener': 'bg-success',
        'Landmark': 'bg-primary',
        'Multitool': 'bg-info',
        'Manual': 'bg-secondary',
        'Other': 'bg-secondary'
      };
      return methodMap[method] || 'bg-dark';
    }
  },
  mounted() {
    // Set initial active tab based on available data
    if (this.courtListenerCount > 0) {
      this.activeTab = 'courtlistener';
    } else if (this.verifiedCount > 0) {
      this.activeTab = 'verified';
    } else if (this.unverifiedCount > 0) {
      this.activeTab = 'unverified';
    } else if (this.hasUnlikelyCitations) {
      this.activeTab = 'unlikely';
    }
  }
};
</script>

<style scoped>
.reusable-results {
  margin-bottom: 2rem;
}

.card {
  border: none;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  overflow: hidden;
}

.card-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.card-body {
  padding: 1.5rem;
}

.nav-tabs {
  border-bottom: 1px solid #dee2e6;
  margin-bottom: 1.5rem;
}

.nav-tabs .nav-link {
  border: none;
  color: #6c757d;
  font-weight: 500;
  padding: 0.75rem 1.25rem;
  margin-right: 0.5rem;
  border-radius: 0.25rem 0.25rem 0 0;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
}

.nav-tabs .nav-link:hover {
  border: none;
  color: #0d6efd;
  background-color: rgba(13, 110, 253, 0.1);
}

.nav-tabs .nav-link.active {
  color: #0d6efd;
  background-color: #fff;
  border-bottom: 3px solid #0d6efd;
  font-weight: 600;
}

.nav-tabs .nav-link i {
  margin-right: 0.5rem;
}

.badge {
  font-weight: 500;
  padding: 0.35em 0.5em;
  font-size: 0.75em;
  letter-spacing: 0.5px;
}

.alert {
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 1.25rem;
}

.alert-success {
  background-color: #d1e7dd;
  color: #0a3622;
}

.alert-warning {
  background-color: #fff3cd;
  color: #664d03;
}

.alert-info {
  background-color: #e7f1ff;
  color: #055160;
}

.table {
  margin-bottom: 0;
}

.table th {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.5px;
  color: #6c757d;
  background-color: #f8f9fa;
  border-bottom: 2px solid #dee2e6;
  padding: 0.75rem 1rem;
}

.table td {
  padding: 0.75rem 1rem;
  vertical-align: middle;
  border-top: 1px solid #f1f1f1;
}

.table-hover tbody tr:hover {
  background-color: rgba(13, 110, 253, 0.03);
}

.citation-stats .card {
  border: 1px solid rgba(0, 0, 0, 0.05);
  box-shadow: none;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.citation-stats .card:hover {
  transform: translateY(-2px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.05);
}

.citation-stats h6 {
  font-size: 0.75rem;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  margin-bottom: 0.25rem;
}

.citation-stats h3 {
  font-weight: 700;
  margin: 0;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .card-body {
    padding: 1rem;
  }
  
  .nav-tabs .nav-link {
    padding: 0.5rem 0.75rem;
    font-size: 0.875rem;
  }
  
  .citation-stats .col-md-4 {
    margin-bottom: 1rem;
  }
}
</style>

