<template>
  <div class="enhanced-file-upload">
    <div class="card">
      <div class="bg-primary text-white p-3 rounded-top">
        <h5 class="mb-0">Upload Document</h5>
      </div>
      <div class="card-body">
        <div class="mb-3">
          <label for="enhancedFileUpload" class="form-label">Select a file to analyze for citations</label>
          <input 
            class="form-control" 
            type="file" 
            id="enhancedFileUpload" 
            ref="fileUpload" 
            @change="handleFileChange" 
            accept=".pdf,.docx,.txt,.rtf,.doc,.html,.htm"
            :disabled="isAnalyzing"
          >
          <div class="form-text">Supported formats: PDF, DOCX, TXT, RTF, DOC, HTML</div>
        </div>
        
        <div v-if="file" class="mb-3">
          <div class="alert alert-info d-flex align-items-center">
            <i class="fas fa-file-alt me-2"></i>
            <div>
              <strong>Selected file:</strong> {{ file.name }} ({{ formatFileSize(file.size) }})
            </div>
          </div>
        </div>
        
        <div class="d-flex align-items-center flex-wrap gap-2">
          <button 
            class="btn btn-primary me-2" 
            @click="analyzeDocument" 
            :disabled="!file || isAnalyzing"
          >
            <span v-if="isAnalyzing" class="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
            {{ isAnalyzing ? 'Analyzing...' : 'Analyze Citations' }}
          </button>
          
          <div v-if="isAnalyzing" class="ms-3 flex-grow-1">
            <div class="progress">
              <div 
                class="progress-bar progress-bar-striped progress-bar-animated" 
                role="progressbar" 
                :style="{ width: progress + '%' }"
                :aria-valuenow="progress" 
                aria-valuemin="0" 
                aria-valuemax="100"
              >
                {{ progress }}%
              </div>
            </div>
          </div>
        </div>
        
        <div v-if="error" class="alert alert-danger mt-3 mb-0">
          <i class="fas fa-exclamation-circle me-2"></i>
          {{ error }}
        </div>
      </div>
    </div>
    
    <!-- Analysis Results -->
    <div v-if="documentAnalysisResult" class="mt-4">
      <CitationReport
        :citation-data="documentAnalysisResult"
        :is-loading="isLoadingReport"
        :error="reportError"
        @refresh="refreshReport"
      />
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import CitationReport from './CitationReport.vue';

export default {
  name: 'EnhancedFileUpload',
  components: {
    CitationReport
  },
  data() {
    return {
      file: null,
      isAnalyzing: false,
      documentAnalysisResult: null,
      error: null,
      progress: 0,
      isLoadingReport: false,
      reportError: null,
      hasResults: false
    };
  },
  computed: {
    basePath() {
      // Determine the base path for API requests
      const path = window.location.pathname;
      if (path.includes('/casestrainer/')) {
        return '/casestrainer';
      } else {
        return '';
      }
    }
  },
  methods: {
    handleFileChange(event) {
      this.file = event.target.files[0];
      this.documentAnalysisResult = null;
      this.error = null;
      this.reportError = null;
      this.hasResults = false;
    },
    
    /**
     * Refresh the citation report display
     */
    refreshReport() {
      if (this.documentAnalysisResult) {
        this.displayCitationReport(this.documentAnalysisResult);
      }
    },
    
    /**
     * Display the citation report using the citation-report.js library
     * @param {Object} data - The citation data to display
     */
    displayCitationReport(data) {
      this.isLoadingReport = true;
      this.reportError = null;
      
      try {
        // Check if we have results
        this.hasResults = data?.citations?.length > 0 || 
                         data?.validation_results?.length > 0;
        
        // Update the document analysis result to trigger re-render of CitationReport
        this.documentAnalysisResult = { ...data };
        
      } catch (error) {
        console.error('Error preparing citation report:', error);
        this.reportError = 'An error occurred while preparing the citation report';
      } finally {
        this.isLoadingReport = false;
      }
    },
    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes';
      const k = 1024;
      const sizes = ['Bytes', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    },
    async analyzeDocument() {
      if (!this.file) {
        this.error = 'Please select a file to upload';
        return;
      }
      
      this.isAnalyzing = true;
      this.progress = 0;
      this.documentAnalysisResult = null;
      this.error = null;
      this.reportError = null;
      this.hasResults = false;
      
      try {
        const formData = new FormData();
        formData.append('file', this.file);
        
        // Add progress handler for upload
        const config = {
          headers: { 'Content-Type': 'multipart/form-data' },
          onUploadProgress: (progressEvent) => {
            this.progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          }
        };
        
        // Make the API request
        const response = await axios.post(`/casestrainer/api/analyze`, formData, config);
        
        // Process the response
        const result = response.data;
        
        // Transform the response to match the expected format for citation-report.js
        const formattedResults = this.formatResultsForCitationReport(result);
        
        // Display the citation report
        this.displayCitationReport(formattedResults);
        
      } catch (error) {
        console.error('Error analyzing document:', error);
        this.error = error.response?.data?.message || error.message || 'An error occurred while analyzing the document';
        
        // Log detailed error for debugging
        if (error.response) {
          console.error('Error response:', error.response.data);
          console.error('Status:', error.response.status);
          console.error('Headers:', error.response.headers);
        } else if (error.request) {
          console.error('No response received:', error.request);
        } else {
          console.error('Error setting up request:', error.message);
        }
      } finally {
        this.isAnalyzing = false;
        this.progress = 100;
      }
    },
    formatResultsForCitationReport(result) {
      if (!result) return { citations: [] };
      
      // If we already have the citations array, use it directly
      if (result.citations && Array.isArray(result.citations)) {
        return {
          ...result,
          citations: result.citations.map(citation => ({
            ...citation,
            // Ensure required fields exist
            id: citation.id || Math.random().toString(36).substr(2, 9),
            citation_text: citation.citation_text || citation.citation || citation.text || '',
            case_name: citation.case_name || citation.title || '',
            verified: citation.verified || false,
            validation_method: citation.validation_method || citation.source || 'Unknown',
            context: citation.context || '',
            timestamp: citation.timestamp || new Date().toISOString(),
            metadata: {
              ...(citation.metadata || {}),
              confidence: citation.confidence,
              jurisdiction: citation.jurisdiction,
              citation_type: citation.citation_type
            }
          }))
        };
      }
      
      // Transform validation_results to the citation report format
      if (result.validation_results && Array.isArray(result.validation_results)) {
        const citations = result.validation_results.map(item => ({
          id: item.id || Math.random().toString(36).substr(2, 9),
          citation_text: item.citation || item.text || '',
          case_name: item.case_name || item.title || '',
          verified: item.verified || false,
          validation_method: item.validation_method || item.source || 'Unknown',
          context: item.context || '',
          timestamp: item.timestamp || new Date().toISOString(),
          metadata: {
            ...(item.metadata || {}),
            confidence: item.confidence,
            jurisdiction: item.jurisdiction,
            citation_type: item.citation_type
          }
        }));
        
        return { 
          ...result,
          citations,
          message: result.message || 'Citation analysis complete',
          timestamp: new Date().toISOString(),
          document_metadata: result.document_metadata || {}
        };
      }
      
      // Handle case where citations might be in a different format
      if (result.results && Array.isArray(result.results)) {
        const citations = result.results.map(item => ({
          id: item.id || Math.random().toString(36).substr(2, 9),
          citation_text: item.citation || item.text || '',
          case_name: item.case_name || item.title || '',
          verified: item.verified || false,
          validation_method: item.validation_method || item.source || 'Unknown',
          context: item.context || '',
          timestamp: item.timestamp || new Date().toISOString(),
          metadata: {
            ...(item.metadata || {}),
            confidence: item.confidence,
            jurisdiction: item.jurisdiction,
            citation_type: item.citation_type
          }
        }));
        
        return {
          ...result,
          citations,
          message: result.message || 'Citation analysis complete',
          timestamp: new Date().toISOString()
        };
      }
      
      // Default empty result
      return { 
        ...result,
        citations: [],
        message: result.message || 'No citation data available',
        timestamp: new Date().toISOString()
      };
    },
    exportToCSV() {
      if (!this.documentAnalysisResult || !this.documentAnalysisResult.citations) {
        this.error = 'No citation data available to export';
        return;
      }
      
      try {
        // Convert the citations to CSV format
        const headers = ['Citation', 'Case Name', 'Status', 'Validation Method', 'Context'];
        const rows = this.documentAnalysisResult.citations.map(citation => ({
          'Citation': citation.citation_text,
          'Case Name': citation.case_name || '',
          'Status': citation.verified ? 'Verified' : 'Not Verified',
          'Validation Method': citation.validation_method || 'Unknown',
          'Context': citation.context ? citation.context.replace(/[\n\r]+/g, ' ').trim() : ''
        }));
        
        // Convert to CSV
        let csvContent = [
          headers.join(','),
          ...rows.map(row => 
            headers.map(header => 
              `"${String(row[header] || '').replace(/"/g, '""')}"`
            ).join(',')
          )
        ].join('\r\n');
        
        // Create download link
        const blob = new Blob(["\uFEFF" + csvContent], { type: 'text/csv;charset=utf-8;' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.setAttribute('href', url);
        link.setAttribute('download', `citations_${new Date().toISOString().slice(0, 10)}.csv`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        
      } catch (error) {
        console.error('Error exporting to CSV:', error);
        this.error = 'Failed to export citations to CSV';
      }
    }
  },
  // No cleanup needed as the CitationReport component handles its own cleanup
};
</script>

<style scoped>
.enhanced-file-upload {
  margin-bottom: 2rem;
}

/* Citation report styles */
#citation-report-container {
  width: 100%;
  overflow-x: auto;
}

.citation-table {
  width: 100%;
  border-collapse: collapse;
  margin: 1rem 0;
  font-size: 0.9rem;
}

.citation-table th,
.citation-table td {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 1px solid #dee2e6;
  vertical-align: top;
}

.citation-table th {
  background-color: #f8f9fa;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.8rem;
  letter-spacing: 0.5px;
}

.citation-table tr:hover {
  background-color: rgba(0, 0, 0, 0.02);
}

/* Status badges */
.badge {
  font-size: 0.75rem;
  font-weight: 500;
  padding: 0.35em 0.65em;
  border-radius: 0.25rem;
}

/* Tabs */
.nav-tabs {
  border-bottom: 1px solid #dee2e6;
  margin-bottom: 1rem;
}

.nav-tabs .nav-link {
  border: 1px solid transparent;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  padding: 0.5rem 1rem;
  font-weight: 500;
  color: #495057;
  transition: all 0.2s ease-in-out;
}

.nav-tabs .nav-link:hover {
  border-color: #e9ecef #e9ecef #dee2e6;
  color: #0056b3;
}

.nav-tabs .nav-link.active {
  color: #0056b3;
  background-color: #fff;
  border-color: #dee2e6 #dee2e6 #fff;
  border-bottom: 2px solid #0056b3;
}

/* Progress bar */
.progress {
  height: 1.5rem;
  border-radius: 0.25rem;
  background-color: #e9ecef;
  overflow: hidden;
}

.progress-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 0.75rem;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  transition: width 0.6s ease;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .citation-table {
    display: block;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  .nav-tabs .nav-link {
    padding: 0.5rem 0.75rem;
    font-size: 0.85rem;
  }
}

/* Print styles */
@media print {
  .no-print {
    display: none !important;
  }
  
  #citation-report-container {
    width: 100%;
    overflow: visible;
  }
  
  .citation-table {
    page-break-inside: auto;
  }
  
  .citation-table tr {
    page-break-inside: avoid;
    page-break-after: auto;
  }
  
  .citation-table td, 
  .citation-table th {
    padding: 0.5rem;
    font-size: 0.8rem;
  }
}
</style>
